.Main-div {
    display: flex;
    flex-direction: column;
    align-items: center;
}

h1 {
    font-family: monospace, sans-serif;
}

#searchbar {
    font-family: monospace;
    width: 400px;
    height: 30px;

    border: 1px solid rgb(185, 184, 184);
    border-radius: 5px;
}

#button {
    font-family: monospace;
    color: white;
    cursor: pointer;
    background-color: rgb(114, 114, 184);
    height: 35px;
    width: 70px;
    margin-left: 10px;
    border-radius: 5px;
    border: none;
}

#headshot {

    width: 200px;
    object-fit: cover;
    background-color: aqua;
}

.Search-item {  
    height: 30px;
}

.Search-item-container {
 height: 20px;   
}
.Search-item:hover {
    background-color: rgb(244, 244, 244);
}
#Search-container {
    overflow-x: scroll;
    border: 1px solid rgb(186, 186, 186);
    max-height: 300px;
    width: 400px;
    margin-right: 97px;
}


